import React, { useState } from 'react';
import { View, Text, TouchableOpacity, Platform, StyleSheet } from 'react-native';
import { FoilCollectionView } from './FoilCollectionView';
import { LoadingIndicator } from './LoadingIndicator';
import { BaseProduct } from '../types/scraper';
import { Column } from '../constants/layout';
import { useTheme } from '../theme/ThemeContext';
import { trackAction, ANALYTICS_EVENTS } from '../utils/analytics';
import { QuiverViewProps } from '../types/quiver';
import { spacing } from '../theme/spacing';
import { FilterOptions } from '../types/scraper';
import { useApp } from '../context/AppContext';

export const QuiverView: React.FC<QuiverViewProps> = ({
  foils,
  allFoils,
  columns,
  quiverName = 'Untitled Quiver',
  quiverId,
  searchQuery = '',
  onSearch,
  onUpdateQuiver,
  onClose,
  isLoading,
  error,
}) => {
  const { colors } = useTheme();
  const { filters, setFilters } = useApp();
  const [isSettingsVisible, setIsSettingsVisible] = useState(false);

  const handleUpdateFoils = (updatedFoils: BaseProduct[]) => {
    onUpdateQuiver?.(updatedFoils);
    trackAction(ANALYTICS_EVENTS.UPDATE_QUIVER, {
      quiverId,
      quiverName,
      foilCount: updatedFoils.length
    });
  };

  // Handle filter changes
  const handleFilterChange = (type: keyof FilterOptions, value: string) => {
    setFilters(prev => ({
      ...prev,
      [type]: prev[type].includes(value)
        ? prev[type].filter(item => item !== value)
        : [...prev[type], value]
    }));
  };

  const handleClearFilters = () => {
    setFilters({ brands: [], series: [], type: [] });
  };

  if (isLoading) {
    return (
      <View style={styles.centerContainer}>
        <LoadingIndicator />
      </View>
    );
  }

  if (error) {
    return (
      <View style={styles.centerContainer}>
        <Text style={[styles.errorText, { color: colors.error }]}>
          {error}
        </Text>
        <TouchableOpacity
          style={[styles.retryButton, { backgroundColor: colors.primary }]}
          onPress={onClose}
        >
          <Text style={[styles.buttonText, { color: colors.text.inverse }]}>
            Go Back
          </Text>
        </TouchableOpacity>
      </View>
    );
  }

  const renderHeader = () => (
    <View style={styles.header}>
      <Text style={[styles.headerText, { color: colors.text.primary }]}>
        Viewing Saved Quiver: {quiverName}
      </Text>
    </View>
  );

  const renderControls = () => (
    <View style={styles.controlsContainer}>
      <View style={styles.controlsInnerContainer}>
        <View style={styles.controlsColumn}>
          <TouchableOpacity
            style={[styles.button, { backgroundColor: colors.primary }]}
            onPress={() => setIsSettingsVisible(true)}
          >
            <Text style={[styles.buttonText, { color: colors.text.inverse }]}>
              Settings
            </Text>
          </TouchableOpacity>
          <TouchableOpacity
            style={[styles.closeButton, { backgroundColor: colors.primary }]}
            onPress={onClose}
          >
            <Text style={[styles.buttonText, { color: colors.text.inverse }]}>
              Close Quiver
            </Text>
          </TouchableOpacity>
        </View>
      </View>
    </View>
  );

  return (
    <View style={styles.container}>
      <FoilCollectionView
        foils={foils}
        allFoils={allFoils}
        columns={columns}
        searchQuery={searchQuery}
        onSearch={onSearch}
        onUpdateFoils={handleUpdateFoils}
        renderHeader={renderHeader}
        renderControls={renderControls}
        filterOptions={{
          brands: Array.from(new Set(allFoils?.map(f => f.brand) || [])),
          series: Array.from(new Set(allFoils?.map(f => f.series).filter((s): s is string => !!s) || [])),
          types: Array.from(new Set(allFoils?.map(f => f.type).filter((t): t is 'front' | 'tail' => t === 'front' || t === 'tail') || []))
        }}
        selectedFilters={filters}
        onFilterChange={handleFilterChange}
        onClearFilters={handleClearFilters}
      />
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    position: 'relative',
  },
  centerContainer: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    padding: spacing.LARGE,
  },
  header: {
    padding: spacing.LARGE,
    borderBottomWidth: 1,
  },
  headerText: {
    fontSize: 20,
    fontWeight: '600',
  },
  errorText: {
    fontSize: 16,
    textAlign: 'center',
    marginBottom: spacing.LARGE,
  },
  controlsContainer: {
    position: 'absolute',
    bottom: spacing.CONTROLS_BOTTOM,
    right: 0,
    left: 0,
    paddingHorizontal: spacing.CONTAINER_PADDING,
    zIndex: 1000,
    ...(Platform.OS === 'web' && {
      pointerEvents: 'none',
    }),
  },
  controlsInnerContainer: {
    alignItems: 'flex-end',
  },
  controlsColumn: {
    alignItems: 'flex-end',
    gap: spacing.BUTTON_GAP,
    ...(Platform.OS === 'web' && {
      pointerEvents: 'auto',
    }),
  },
  button: {
    flexDirection: 'row',
    alignItems: 'center',
    justifyContent: 'center',
    paddingHorizontal: spacing.BUTTON_PADDING_H,
    paddingVertical: spacing.BUTTON_PADDING_V,
    borderRadius: 8,
    gap: spacing.SMALL,
    minHeight: 32,
    minWidth: 90,
    ...(Platform.OS === 'web' && {
      cursor: 'pointer',
      pointerEvents: 'auto',
      userSelect: 'none',
      boxShadow: '0px 2px 4px rgba(0,0,0,0.1)',
    }),
  },
  closeButton: {
    paddingHorizontal: spacing.LARGE,
    paddingVertical: spacing.MEDIUM,
    borderRadius: 8,
    ...(Platform.OS === 'web' && {
      cursor: 'pointer',
      pointerEvents: 'auto',
      userSelect: 'none',
      boxShadow: '0px 2px 4px rgba(0,0,0,0.1)',
    }),
  },
  retryButton: {
    paddingHorizontal: spacing.LARGE,
    paddingVertical: spacing.MEDIUM,
    borderRadius: 8,
    marginTop: spacing.MEDIUM,
    ...(Platform.OS === 'web' && {
      cursor: 'pointer',
    }),
  },
  buttonText: {
    fontSize: 14,
    fontWeight: '600',
    ...(Platform.OS === 'web' && {
      userSelect: 'none',
    }),
  },
}); 